.countdown_item {
  font-family: $font-primary;
  font-size: 25px;
}

.w-fit {
  width: fit-content;
}

.box_counter {
  background: #ffffff;
  border: solid 1px #e2eef1;
  padding: 2px 10px;
  border-radius: 5px;
  box-shadow: 0px 17px 13px 0px #192c4b05;
}

.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  word-break: break-all;
}

.line-clamp-3 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  word-break: break-all;
}

a.active-link {
  color: $color_brand;
}

.stepper a,
.stepper span {
  line-height: 32px !important;
}
